body {
  background-color: #8a2be2;
}

.card {
  width: 252px;
  height: 265px;
  background: #fff;
  border-radius: 30px;
  margin-top: 20px;
  transition: all .2s ease-in-out;
  box-shadow: 5px 5px 10px #bebebe, -15px -15px 30px #fff;
}

.img {
  width: 100%;
  height: 50%;
  align-items: top;
  background: linear-gradient(#e66465, #9198e5);
  border-top-left-radius: 30px;
  border-top-right-radius: 30px;
  justify-content: right;
  display: flex;
}

.save {
  width: 30px;
  height: 30px;
  background-color: #fff;
  border-radius: 10px;
  justify-content: center;
  align-items: center;
  margin: 20px;
  transition: all .2s ease-in-out;
  display: flex;
}

.text {
  align-items: space-around;
  flex-direction: column;
  margin: 20px;
  display: flex;
}

.save .svg {
  width: 15px;
  height: 15px;
  transition: all .2s ease-in-out;
}

.icon-box {
  width: 70%;
  background-color: #e3fff9;
  border-radius: 10px;
  justify-content: left;
  align-items: center;
  margin-top: 15px;
  padding: 10px;
  display: flex;
}

.icon-box svg {
  width: 17px;
}

.text .h3 {
  font-family: "Lucida Sans" sans-serif;
  color: #000;
  font-size: 15px;
  font-weight: 600;
}

.text .p {
  font-family: "Lucida Sans" sans-serif;
  color: #999;
  font-size: 13px;
}

.icon-box .span {
  font-family: "Lucida Sans" sans-serif;
  color: #9198e5;
  margin-left: 10px;
  font-size: 13px;
  font-weight: 500;
}

.card:hover {
  cursor: pointer;
  box-shadow: 0 10px 20px rgba(0, 0, 0, .1);
}

.save:hover {
  transform: scale(1.1)rotate(10deg);
}

.save:hover .svg {
  fill: #ced8de;
}

